body {
    font-family: $base-font-family;
    font-size: $base-font-size;
    line-height: $base-line-height;
    font-weight: 300;
    color: $text-color;
    height: 100%;
    background-color: $background-color;
    background-image: url("data:image/svg+xml;base64,"); }

.page-content {
    min-height: 100%; }

.post-title {
    text-align: center; }

.html-inverted {
    @include invert(); }

.body-embiggened {
    font-size: $base-font-size * 1.5; }

.container {
    padding: 20px; }

#pitchslides {
    text-align: center;
    @include padding(20vh 5em 10px 5em);
    .item {
        height: 15em; } }

.emphasis {
    padding: 3px 4px;
    color: red;
    background-color: #f7f7f9;
    border: 1px solid #e1e1e8; }

$mobile: new-breakpoint(max-width 500px 4);
.responsive-container {
    padding-top: 100px;
    @include outer-container;
    article {
        @include span-columns(6);
        @include media($mobile) {
            @include span-columns(4); }
        .bullet-img {
            display: block;
            height: 10em;
            margin: auto;
            margin-bottom: 1em;
            margin-left: 0px; } } }

.responsive-container-single {
    @include outer-container;
    article {
        background-color: transparentize(white, .2);
        @include media( $medium-screen ) {
            padding: 10px; }
        .bullet-img {
            display: block;
            height: 10em;
            margin: auto;
            margin-bottom: 1em;
            margin-left: 0px; } } }

.page-title {
    border-bottom: 2px solid;
    display: inline-block;
    font-size: 1em;
    font-weight: 800;
    margin-bottom: 2em;
    padding: .1em 0;
    text-transform: uppercase; }

