// Our variables
$base-font-family:	'Oxygen', "Helvetica Neue", Helvetica, Arial, sans-serif;
$sans-serif:		$base-font-family;
$alt-sans-serif:	'Oxygen', sans-serif;
$serif: 			'PT Serif', Cambria, "Times New Roman", Times, serif;
$article:			'Oxygen', sans-serif;

$base-font-size:	16px;
$small-font-size:	$base-font-size * 0.875;
$base-line-height:	1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #FFDC00;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

$on-palm:          600px;
$on-laptop:        800px;


