.footer {
  $footer-background: darken($brand-color, 2);
  $footer-color: black;
  $footer-link-color: transparentize($footer-color, .4);
  $footer-disclaimer-color: transparentize($footer-color, .2);

  text-align: center;
  background: $footer-background;
  background-image: url("data:image/svg+xml;base64,");
  padding: $base-spacing $gutter;
  width: 100%;
  margin-bottom: 3em;
  @include media( $medium-screen ) {
    margin-bottom: 0em;
  }
  @include media( $large-screen ) {
    margin-bottom: 0em;
  }

  .footer-logo {
    text-align: center;
    margin-bottom: 2em;

    img {
      height: 3em;
    }
  } 

  .footer-links {
    @include clearfix;
    @include outer-container;
    margin-bottom: $base-spacing;
    @include media( $medium-screen ) {
      margin-bottom: 0;
    }
  }


  li {
    font-weight: 400;
    text-align: center;
    @include media($medium-screen) {
      text-align: center;
    }
  }

  li a {
    color: $footer-link-color;

    &:hover {
      color: transparentize($footer-color, 0);
    }
  }

  li h3 {
    color: $footer-color;
    font-size: 1em;
    font-weight: 800;
    margin-bottom: .4em;
  }

  li img.dunedinlogo {
    height: 4em;
  }

  hr {
    border: 1px solid transparentize($footer-disclaimer-color, .3);
    margin: 0 auto $base-spacing;
    width: 12em;
  }

  p {
    color: $footer-disclaimer-color;
    font-size: .9em;
    line-height: 1.5em;
    margin: auto;
    max-width: 35em;
    text-align: center;
  }
}
